<template>
  <section class="my-tab">
    <div v-for="(item,key) in list" :class="{mySelected:item.id == active}" @click="swapTab(item.id)"><label>{{item.categoryName}}</label></div>
  </section>
</template>

<script>
  export default {
    props: ['action', 'activeId'],
    data () {
      return {
      }
    },
    methods: {
      swapTab: function (val) {
        this.$emit('changeTab', val)
      }
    },
    created: function () {
      this.activeTab = this.active
    },
    computed: {
      list: function () {
        return this.action
      },
      active: function () {
         return this.activeId
      }
    }
  }
</script>

<style>

</style>


