<template>
  <div class="store-view">
    <section class="store-banner" :style="style">
      <img :src="store.imageUrl">
      <i class="store-back" @click="toback"></i>
      <i class="store-share"></i>
      <i class="store-collection" @click="collection(store.id)"></i></section>
    <section class="store-mess">
      <header>
        <div class="store-title">{{store.name}}</div><small>{{store.description}}</small></header>
      <div class="store-mess-content">
        <div class="address-icon"><i></i></div>
        <div class="address-text">
          <div class="store-address">{{store.address}}</div>
          <div class="store-distance">距您<span>{{store.distance}}米</span></div>
        </div>
        <div class="telphone"><a :href="phone"><i></i></a></div>
      </div>
    </section>
    <section>
      <div class="scorll-tab">
        <tab-view :action="tabAction" @changeTab="changeTab" :activeId="active"></tab-view>
      </div>
      <div class="store-menu-list" :style="height">
            <div class="hi-life-list-item" v-for="(item,key) in categorys" @click="toStoreDetail(item.skuId,store.latitude,store.longitude)">
              <div class="hi-life-list-item-img"><img :src="item.imageUrl"></div>
              <div class="hi-life-list-item-content">
                <h3>{{item.name}}</h3>
                <div class="hi-life-list-item-summary">{{item.summary}}</div>
                <div class="hilife-list-detail"><span class="sp-sales">¥{{item.sellPrice}}</span>
                  <div class="hi-life-list-item-oper" @click.stop="addSp(item.skuId)"></div>
                </div>
              </div>
            </div>
        
      </div>
    </section>
    <div class="store-shop-cart">
      <div class="shop-cart" @click="toCart"><i v-if="cartSize>0">{{cartSize}}</i></div>
      <div class="youhui-btn" @click="preferentialpay">优惠买单</div>
    </div>
  </div>
</template>

<script>
  import {getStore, getProductCategory, addCart,addCollectStore} from '../../service/getData'
  import tab from '@/components/tab/tab'
  export default {
    components: {
      'tab-view': tab
    },
    data () {
      return {
        store: {},
        style: {},
        height: {
          height: ''
        },
        cartSize: 0,
        active: 7,
        tabAction: [],
        routerName: '',
        categorys:[],
        phone: ''
      }
    },
    created: function () {
      var _this = this
      let latitude = this.$route.query.latitude
      let longitude = this.$route.query.longitude
//    this.height.height = document.body.clientHeight - 372 + 'px'
      getStore(this.$route.query.storeId,latitude,longitude)
        .then(function (response) {
          _this.store = response.data
          _this.tabAction = response.data.categorys
          if (response.data.phone !== '') {
            _this.phone = 'tel:' + response.data.phone
          } else {
            _this.phone = 'javascript:void(0)'
          }
        })
        .catch(function () {
          _this.$toast('加载失败')
        })
      this.getCategorysList(this.active)
    },
    methods: {
      /* 收藏商家*/
      collection: function (storeId) {
        let _this = this
        addCollectStore(storeId)
        .then(res => {
          if (res.data.code === 1) {
            _this.$toast('收藏成功!')
          }
        })
        .catch(res => {
          _this.$toast('收藏失败')
        })
      },
      changeTab: function (val) {
        this.active = val
        this.getCategorysList(val)
      },
      toCart: function () {
        this.$router.push({
          name: 'shopCart'
        })
      },
      preferentialpay: function () {
        this.$router.push({
          name: 'preferentialpay'
        })
      },
      addSp: function (val) { // 添加商品的id
        var _this = this
        addCart(val)
          .then(res => {
            if (res.data.code === 1) {
              _this.cartSize++
            }
            if (res.data.code === '401') {
              _this.$router.push({name:'login'})
            }
          })
          .catch(() => {
            _this.$toast('添加失败')
          })
      },
      toStoreDetail: function (skuId,latitude,longitude) {
        this.$router.push({
          name: 'good',
          query: {
            skuId: skuId,
            latitude: latitude,
            longitude: longitude
          }
        })
      },
      toback: function () {
        this.$router.go(-1)
      },
      getCategorysList: function (val) {
        let _this = this
        getProductCategory(val)
        .then(res => {
          _this.categorys = res.data
        })
        .catch(() => {
          _this.$toast('请求失败')
        })
      }
    }
  }
</script>

<style>
/* 商店首页 */
.store-view {
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.store-banner {
  height: 1.59rem;
  position: relative;
  background-color: gainsboro;
}

.store-banner i {
  position: absolute;
  width: .3rem;
  height: .3rem;
}

.store-back {
  top: .01rem;
  left: .15rem;
  background: url(/static/image/classify/classify_back@3x.png);
  background-size: 100% 100%;
}

.store-share {
  top: .01rem;
  right: .55rem;
  background: url(/static/image/classify/classify_share@3x.png);
  background-size: 100% 100%;
}

.store-collection {
  top: .01rem;
  right: .15rem;
  background: url(/static/image/store/store_collect@3x.png);
  background-size: 100% 100%;
}


.store-menu-list{
  overflow: auto;
  padding-bottom: .52rem;
  -webkit-overflow-scrolling: touch;
}
.store-shop-cart{
  position: fixed;
  height: .5rem;
  line-height: .5rem;
  width: 100%;
  bottom: 0;
  background-color: rgba(0,0,0,.8);
}
.youhui-btn{
  float: right;
  width: 1.05rem;
  height: 100%;
  color: #FFFFFF;
  text-align: center;
    background: -webkit-linear-gradient(left, rgb(255, 161, 38), rgb(255, 139, 32));
    background: linear-gradient(to right, rgb(255, 161, 38), rgb(255, 139, 32));
}
.store-view .hi-life-list-item{
  padding-left: .15rem;
}
.shop-cart{
  position: relative;
  display: inline-block;
  height: .47rem;
  width: .47rem;
  margin-left: .2rem;
  margin-top: -.1rem;
  background: url(/static/image/store_list_add_cart@3x.png);
  background-size: 100% 100%;
}
.shop-cart>i{
  position: absolute;
  z-index: 1;
  right: 0;
  height: .16rem;
  width: .16rem;
  background-color: red;
  color: #FFFFFF;
  line-height: .16rem;
    font-style: normal;
    text-align: center;
  border-radius: 50%;
}
</style>


